import { 
  getCardRecordOf,
  getCurrentCardIndex
} from "@/utils/config-helper";
import { currentLangId } from "@/utils/lang-helper";

export function getDetailViewConfig() {
  const cardIndex = getCurrentCardIndex();
  return getCardRecordOf(cardIndex)![currentLangId.value];
}

export function getDetailViewImgFrom(imgSrc: string) {
  const cardIndex = getCurrentCardIndex();
  return `${process.env.BASE_URL}config-assets/detail-view/card-${cardIndex}/${imgSrc}`;
}
