
import { getAssetConfig } from "@/utils/config-helper";
import { defineComponent } from "vue"; 
import { getDetailViewImgFrom } from "../../util-helper/config-asset";

export default defineComponent({
  name: "DetailBanner",
  components: {
  },
  props: {
  },
  setup() {
    return {};
  },
  data: () => {
    return {};
  },
  computed: {
    bannerList() {
      const assetConfig = getAssetConfig()?.detailView;
      if (assetConfig) {
        return assetConfig.banner;
      }
      return [];
    },
  },
  watch: {
  },
  methods: {
    getBannerSrcFrom(bannerSrc: string) {
      return getDetailViewImgFrom(bannerSrc);
    },
  },
});

